import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { HttpClientModule } from '@angular/common/http';

import { AppComponent } from './app.component';
import { PaymentComponent } from './pages/payment/payment.component';
import { QrCodeModule } from 'interblockchain-components';
import { PaymentSelectionComponent } from './pages/payment-selection/payment-selection.component';
import { ConfirmationsComponent } from './pages/confirmations/confirmations.component';
import { SummaryComponent } from './pages/summary/summary.component';
import { KeyFactoryService } from './services/key-factory/key-factory.service';
import { PaymentSwService } from './services/payment-sw/payment-sw.service';
import { ProgressBarModule } from 'interblockchain-components';

@NgModule({
  declarations: [
    AppComponent,
    PaymentComponent,
    PaymentSelectionComponent,
    ConfirmationsComponent,
    SummaryComponent,
  ],
  imports: [
    HttpClientModule,
    BrowserModule,
    QrCodeModule,
    ProgressBarModule,
    RouterModule.forRoot([
      // { path: '', pathMatch: 'full', component: PaymentSelectionComponent },
      { path: '', pathMatch: 'full', component: PaymentComponent },
      { path: 'confirmations', pathMatch: 'full', component: ConfirmationsComponent },
      // { path: 'summary', pathMatch: 'full', component: SummaryComponent }
    ])
  ],
  providers: [KeyFactoryService, PaymentSwService],
  bootstrap: [AppComponent]
})
export class AppModule {
  constructor(sw: PaymentSwService) {
    sw.init();
  }
}
