import { Component, OnInit } from '@angular/core';
import { PaymentSwService } from '../../services/payment-sw/payment-sw.service';
import { map } from 'rxjs/operators';
import { combineLatest } from 'rxjs';

@Component({
  selector: 'app-confirmations',
  templateUrl: './confirmations.component.html',
  styleUrls: ['./confirmations.component.scss']
})
export class ConfirmationsComponent implements OnInit {

  public validation = combineLatest(
    this.sw.currentValidation,
    this.sw.currentTransaction
  ).pipe(
    map(([{ info, isCompleted }, tx]) => ({
      currentNbConf: info.currentNbConf,
      requiredNbConf: info.requiredNbConf,
      ticker: info.network,
      amount: tx.amount,
      isCompleted,
    }))
  );

  constructor(
    private sw: PaymentSwService,
  ) { }

  ngOnInit() {
  }

  confirm() {
    this.sw.confirmPayment();
  }

}
