import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../environments/environment';

export interface KeyFactoryResponse {
  id: string;
  token: string;
  key: string;
}

@Injectable({
  providedIn: 'root'
})
export class KeyFactoryService {

  constructor(
    private http: HttpClient
  ) { }

  public getKey(id: string, ticker: string) {
    return this.http.get<KeyFactoryResponse>(`${environment.keyFactory}${id}/${ticker}`);
  }
}
